/**
 * Vuex
 * http://vuex.vuejs.org/zh-cn/intro.html
 */
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

//stomp客户端
var stompClient;
//发往服务器的即时消息
var redirectMessage = {content: "", receiver: "", sender: "", date: ""};

const now = new Date();
const store = new Vuex.Store({
    state: {
        // 当前用户
        user: {
            name: 'coffce',
            img: './message/images/1.jpg'
        },
        // 会话列表
        sessions: [
            {
                id: 1,
                user: {
                    name: '私信助手',
                    img: './message/images/2.png'
                },
                messages: [
                    {
                        content: 'Hello，这里是私信界面，所有消息只有双方可见。',
                        date: now
                    }, {
                        content: '使用 Ctrl + Enter 发送消息',
                        date: now
                    }
                ]
            },
            {
                id: 2,
                user: {
                    name: '通知',
                    img: './message/images/3.jpg'
                },
                messages: [
                    {
                        content: 'Hello，这里是通知界面，所有消息会在第一时间通知您。',
                        date: now
                    }
                ]
            }
        ],
        // 当前选中的会话
        currentSessionId: 1,
        // 过滤出只包含这个key的会话
        filterKey: ''
    },
    mutations: {
        INIT_DATA (state) {
            //加载localstorage的聊天记录
            let data = localStorage.getItem('vue-chat-session');
            if (data) {
                state.sessions = JSON.parse(data);
            }

            //向服务器请求聊天记录
            var username = localStorage.getItem("currentUser_name");
            $.ajax({
                type: "GET",
                url: "/chat/get-chats",
                async: true,
                data: {
                    username: username,
                },

                success: function (res) {
                    var chats = JSON.parse(res).data.chats;
                    if (chats) {
                        //将聊天记录加载到页面
                        state.user = chats.user;
                        state.sessions = chats.sessions;
                    }
                },
                error: function (e) {
                    alert("请求出错！");
                }
            });

            //建立websocket连接
            connect(state);
        },
        // 发送消息
        SEND_MESSAGE ({ sessions, currentSessionId }, content) {
            let session = sessions.find(item => item.id === currentSessionId);
            var date = new Date();
            session.messages.push({
                content: content,
                date: date,
                self: true
            });
            // 向服务器发送消息
            // 点对点消息，消息中必须包含对方的username
            // 匹配后端ChatController中的 @MessageMapping("/chat")
            redirectMessage.content = content;
            redirectMessage.receiver = session.user.name.toString();
            redirectMessage.sender = localStorage.getItem("currentUser_name");
            redirectMessage.date = formatDate(date);
            stompClient.send("/app/chat", {}, JSON.stringify(redirectMessage));
        },
        // 选择会话
        SELECT_SESSION (state, id) {
            state.currentSessionId = id;
        } ,
        // 搜索
        SET_FILTER_KEY (state, value) {
            state.filterKey = value;
        }
    }
});

store.watch(
    (state) => state.sessions,
    (val) => {
        console.log('CHANGE: ', val);
        localStorage.setItem('vue-chat-session', JSON.stringify(val));
    },
    {
        deep: true
    }
);

/**
 * 建立websocket连接
 */
function connect(state) {
    var socket = new SockJS('/websocket');
    stompClient = Stomp.over(socket);
    stompClient.connect({}, function (frame) {

        // 订阅 /topic/notice 实现群聊
        // stompClient.subscribe('/topic/notice', function (message) {
        //     showMessage(JSON.parse(message.body));
        // });

        // 订阅 /user/topic/chat 实现点对点聊
        stompClient.subscribe('/user/topic/chat', function (res) {
            var msg = JSON.parse(res.body);
            //加载新的消息
            let session = state.sessions.find(item => item.user.name === msg.sender);
            session.messages.push({
                content: msg.content,
                date: msg.date,
                self: false
            });
        });
    });
}

//格式化时间
var formatDate = function (date) {
    var y = date.getFullYear();
    var m = date.getMonth() + 1;
    m = m < 10 ? ('0' + m) : m;
    var d = date.getDate();
    d = d < 10 ? ('0' + d) : d;
    var h = date.getHours();
    var minute = date.getMinutes();
    minute = minute < 10 ? ('0' + minute) : minute;
    var second= date.getSeconds();
    second = minute < 10 ? ('0' + second) : second;
    return y + '-' + m + '-' + d+' '+h+':'+minute+':'+ second;
};

export default store;
export const actions = {
    initData: ({ dispatch }) => dispatch('INIT_DATA'),
    sendMessage: ({ dispatch }, content) => dispatch('SEND_MESSAGE', content),
    selectSession: ({ dispatch }, id) => dispatch('SELECT_SESSION', id),
    search: ({ dispatch }, value) => dispatch('SET_FILTER_KEY', value)
};



